<template>
  <div>
    <!-- <van-nav-bar left-text="返 回" @click-left="$router.go(-1)" left-arrow style="background:#fff;height:40pt;border-bottom:1px solid #ccc;" fixed></van-nav-bar> -->
    <div class="list">
      <div class="item flex-between" v-for="(item,index) in data" :key="index">
        <div class="left flex_1">
          <div id="column">
            <span class="name">{{item.consignee}}</span>
            <span class="phone">{{item.mobile}}</span>
            <span
              class="default"
              :style="{background:item.is_default==1?'#FF4C23':'#cccccc'}"
              @click="default_(item.address_id)"
            >默认</span>
          </div>
          <div class="address">{{item.province}} {{item.city}} {{item.district}}</div>
        </div>
        <div class="right flex-between">
          <img
            src="../../assets/images/address_mdi.png"
            @click="$router.push({path: '/setAddress',query:{data:item}})"
          />
          <img src="../../assets/images/address_del.png" @click="del(item.address_id)" />
        </div>
      </div>
    </div>
    <div class="button flex-center" @click="$router.push({path: '/setAddress'})">
      <span class="add">+</span>
      <span class="ft">新增地址</span>
    </div>
  </div>
</template>

<script>
import { Icon, Dialog, Toast, NavBar } from "vant";
import axios from "axios";
import url from "@/axios/api.js";
export default {
  components: {
    "van-icon": Icon,
    vanNavBar: NavBar
  },
  data() {
    return {
      data: [],
      is_default: true
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();

      params.append("openid", localStorage.getItem("openid"));
      axios.post(url.address, params).then(res => {
        this.data = res.data.data;
      });
    },
    del(item, address_id) {
      Dialog.confirm({
        title: "提示",
        message: "删除地址后不可恢复！"
      }).then(() => {
        let params = new URLSearchParams();

        params.append("openid", localStorage.getItem("openid"));
        params.append("id", item);
        axios.post(url.delAddress, params).then(res => {
          var data_ = this.data;
          for (var i = 0; i < data_.length; i++) {
            if (data_[i].address_id == item) {
              data_.splice(i, 1);
            }
          }
        });
      });
    },

  }
};
</script>

<style scoped lang="scss">
* {
  margin: 0;
  padding: 0;
}
.list {
  padding: 0 10px;
  background: #fff;
  margin-bottom: 150px;
  .item {
    border-bottom: 1px solid #eee;
    display: flex;
    align-items: center;
    padding: 10px 0;
    .left {
      overflow: hidden;
      padding-right: 10px;
      #column {
        display: flex;
        align-items: center;
        justify-content: unset !important;
        height: 50px;
        font-size: 16px;
        color: #201b18 !important;
        font-weight: 700;
        .phone {
          margin: 5px 20px 0;
          color: #201b18 !important;
        }
        .name {
          color: #201b18 !important;
        }
        .default {
          font-size: 12px;
          padding: 2px 14px;
          border-radius: 30px;
          background: #ff4c23;
          color: #fff;
        }
      }
      .address {
        color: #404040;
      }
    }
    .flex_1 {
      align-items: unset !important;
    }
    .right {
      margin-left: auto;
      img {
        width: 30px;
        height: 30px;
      }
    }
  }
}
.button {
  position: fixed;
  bottom: 0;
  width: 100%;
  color: #f00;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  .add {
    font-size: 25px;
    margin-right: 10px;
  }
  .ft {
    font-size: 16px;
  }
}
</style>



